.App {
  text-align: center;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.modeToggler1{
  background-image: linear-gradient(to bottom, #212529 50% ,#7A0808 50%);
  width:35px;
  height:35px;
  line-height: 35px; 
  text-decoration: none;
  text-align: center;
  border-radius: 50%;
  border: none;
  margin-right: 10px;
  
}

